<script>
/* eslint-disable no-console */
import Header from '@/components/Header.vue'
import firebase from 'firebase/app'
import "firebase/auth"
import "firebase/database"

export default {
  name: 'Home',
  components: {
    Header,
    // Posts,
  },
  data () {
      return {
        count: 10,
        loading: false,
        tweetList: [],//取得したtweet
        user: {},  // ユーザー情報
        input: '',  // 入力したメッセージ
        dialogVisible: false,
      }
  },
  created() {
    firebase.auth().onAuthStateChanged(user => {
      this.user = user ? user : {}
      const ref_tweets = firebase.database().ref('tweets')
      if (user) {
        this.tweetList = []
        // tweets に変更があったときのハンドラを登録
        ref_tweets.limitToLast(20).on('child_added', this.childAdded)
      } else {
        // tweets に変更があったときのハンドラを解除
        ref_tweets.limitToLast(20).off('child_added', this.childAdded)
      }
    })
  },

  methods: {
    //子からのデータを代入
    childValue(value) {
      this.dialogVisible = value
      // console.log(this.dialogVisible)
    },
    // ログイン処理
    logIn() {
      var provider = new firebase.auth.GoogleAuthProvider()
      firebase.auth().signInWithPopup(provider)
        .then(obj => {
          console.log('Create account: ' + obj.user.email)
          // this.$router.push('/')
        })
        .catch(error => alert(error.message))
    },
    // ログアウト処理
    logOut() {
      firebase.auth().signOut()
      // this.$forceUpdate();
    },
    // 受け取ったメッセージをtweetListに追加
    // データベースに新しい要素が追加されると随時呼び出される
    childAdded(snap) {
      const tweets = snap.val()
      this.tweetList.push({
        key: snap.key,
        tweet: tweets.tweet,
        userID: tweets.userID,
        userName: tweets.userName,
        image: tweets.image
      })
    },
    doSend() {
      if (this.user.uid && this.input.length) {
        // firebase にメッセージを追加
        firebase.database().ref('tweets').push({
          tweet: this.input,
          userID: this.user.displayName,
          userName: this.user.displayName,
          image: this.user.photoURL
        }, () => {
          this.input = '' // フォームを空にする
        })
      }
    }
  },
}
</script>


